/* base */

input {
  --input-background-idle: var(--color-background-input-base-idle);
  --input-background-hover: var(--color-background-input-base-hover);
  --input-border-idle: var(--color-border-input-base-idle);
  --input-border-focus: var(--color-border-component-base-focus);
  --input-effect-focus: var(--effect-component-base-focus);

  font-family: var(--font-family);
  font-size: var(--font-size-medium);
  line-height: var(--font-line-height-medium);

  $border-width: 1px;
  border-width: var(--size-none);
  border-radius: var(--radius-x-small);

  padding-inline: calc(var(--size-small) - $border-width);
  padding-block: calc(var(--size-x-small) - $border-width);

  background: var(--input-background-idle);
  border: $border-width solid var(--input-border-idle);
  outline: 1px solid transparent;

  color: var(--color-text-primary);
}

input:disabled,
input[theme-variant]:disabled {
  background: var(--input-background-idle) !important;
  opacity: var(--ratio-opacity-disabled);
  cursor: not-allowed;
  // user-select: none;
}

input:enabled:hover {
  background: var(--input-background-hover);
}

input:enabled:focus,
input:enabled:focus-visible {
  background: var(--input-background-hover);
  border-color: var(--color-border-component-base-focus);
  box-shadow: var(--input-effect-focus);
}

// input[type="checkbox"]:checked:focus,
// input[type="checkbox"]:checked:focus-visible {
//   border-color: var(--color-border-component-accent-focus);
//   box-shadow: var(--effect-component-accent-focus);
// }

input[theme-variant="flat"] {
  --input-background-idle: var(--color-background-input-flat-idle);
  --input-background-hover: var(--color-background-input-flat-hover);
  --input-border-idle: transparent;
}
