:root[theme="light"] {
  --color-text-primary-alpha: var(--color-base-700-alpha);
  --color-text-secondary-alpha: var(--color-base-500-alpha);
  --color-text-tertiary-alpha: var(--color-base-300-alpha);

  --color-text-primary: var(--color-base-700);
  --color-text-secondary: var(--color-base-500);
  --color-text-tertiary: var(--color-base-300);

  --color-background-primary: var(--color-base-75);
  --color-background-secondary: var(--color-base-125);
  --color-background-tertiary: var(--color-base-150);

  /* state */
  --color-background-error: var(--color-error-150);
  --color-text-error: var(--color-error-700);
  --color-border-error: var(--color-error-200);

  --color-background-warning: var(--color-warning-150);
  --color-border-warning: var(--color-warning-200);
  --color-text-warning: var(--color-warning-700);

  --color-background-success: var(--color-success-150);
  --color-border-success: var(--color-success-200);
  --color-text-success: var(--color-success-700);

  /* component */
  --color-text-component-accent-idle: var(--color-accent-50);

  --color-background-component-accent-idle: var(--color-accent-500);
  --color-background-component-accent-hover: var(--color-accent-450);
  --color-background-component-accent-active: var(--color-accent-550);

  --color-border-component-base-focus: var(--color-accent-400);
  --color-border-component-accent-focus: var(--color-accent-150);

  /* button */
  --color-text-button-secondary-idle: var(--color-text-primary);

  --color-background-button-secondary-idle: var(--color-base-200);
  --color-background-button-secondary-hover: var(--color-base-175);
  --color-background-button-secondary-active: var(--color-base-225);

  --color-background-button-flat-idle: var(--color-base-150-alpha);
  --color-background-button-flat-hover: var(--color-base-150-alpha);
  --color-background-button-flat-active: var(--color-base-200-alpha);

  /* input */
  --color-background-input-base-idle: var(--color-base-125);
  --color-background-input-base-hover: var(--color-base-150);

  --color-border-input-base-idle: var(--color-base-200);

  --color-background-input-flat-idle: var(--color-base-125-alpha);
  --color-background-input-flat-hover: var(--color-base-150-alpha);

  /* table */
  --color-background-table-header: var(--color-background-secondary);
  --color-background-table-row-even: var(--color-base-100);
  --color-background-table-row-odd: var(--color-base-75);
  --color-background-table-row-selected: var(--color-accent-450);

  /* effects */
  --effect-none: none;
  --effect-convex: inset 0px -1px 0px 0px rgba(var(--rgb-base-800), 20%);
  --effect-concave: inset 0px 1px 0px 0px rgba(0, 0, 0, 0.1);

  $offset: 1px;
  --effect-component-base-focus: 0px 0px 0px #{$offset} rgba(var(--rgb-accent-400), 35%);
  --effect-component-accent-focus: 0px 0px 0px #{$offset} rgba(var(--rgb-accent-400), 85%);
}
