:root {
  --radius-adjustment-factor: 1;
  --radius-100: 0px;
  --radius-300: calc(2px * var(--radius-adjustment-factor));
  --radius-400: calc(4px * var(--radius-adjustment-factor));
  --radius-500: calc(8px * var(--radius-adjustment-factor));
  --radius-600: calc(16px * var(--radius-adjustment-factor));
  --radius-900: 999px;

  --radius-none: var(--radius-100);
  --radius-x-small: var(--radius-300);
  --radius-small: var(--radius-400);
  --radius-medium: var(--radius-500);
  --radius-large: var(--radius-600);
  --radius-full: var(--radius-900);
}
