/*
  --[category]-[type]-[item]-[usage]-[value?]-[state]

  - category: color, effect, ratio, size
  - type: background, border, outline, text, icon, radius
  - item: component, button, radio-button
  - usage/variant: base, accented, primary, secondary, outlined, flat, ghost, danger, buy-side, sell-side
  - value/internal-state: checked, selected
  - state/user-state: idle, active, hovered, focused, disabled
*/

:root {
  --size-radius-input: var(--radius-x-small);

  --ratio-opacity-disabled: 40%;
}

.primary-content {
  color: var(--color-text-primary-alpha);
}

.secondary-content {
  color: var(--color-text-secondary-alpha);
}

.tertiary-content {
  color: var(--color-text-tertiary-alpha);
}

.primary-content-o {
  color: var(--color-text-primary);
}

.secondary-content-o {
  color: var(--color-text-secondary);
}

.tertiary-content-o {
  color: var(--color-text-tertiary);
}

.demo-surface {
  border-radius: 2px;
  padding: 16px;
}

.background-primary {
  background-color: var(--color-background-primary);
}

.background-secondary {
  background-color: var(--color-background-secondary);
}

.background-tertiary {
  background-color: var(--color-background-tertiary);
}

.banner {
  display: flex;
  flex-direction: row;
  gap: var(--size-medium);
  align-items: center;

  padding-inline: var(--size-medium);
  padding-block: var(--size-medium);
  border-radius: var(--radius-small);
}

.background-error {
  background-color: var(--color-background-error);
  color: var(--color-text-error);
  border: 1px solid var(--color-border-error);
}

.background-warning {
  background-color: var(--color-background-warning);
  color: var(--color-text-warning);
  border: 1px solid var(--color-border-warning);
}

.background-success {
  background-color: var(--color-background-success);
  color: var(--color-text-success);
  border: 1px solid var(--color-border-success);
}

.toggle-item {
  background-color: var(--color-background-input-base-base);

  border-radius: var(--radius-x-small);
  border-width: var(--size-none);

  padding-inline: var(--size-large);
  padding-block: var(--size-x-small);

  color: var(--color-text-secondary);
  font-family: var(--font-family);
  font-size: var(--font-size-medium);
  line-height: var(--font-line-height-medium);
}

.toggle-item:hover {
  background-color: var(--color-background-input-base-hover);

  border-radius: var(--radius-x-small);
}

.toggle-bar {
  border-radius: var(--radius-x-small);

  background-color: var(--color-background-input-base-base);
  display: flex;
  flex-direction: row;
  gap: var(--size-none);
}

.toggle-separator {
  width: 1px;
  margin-block: 4px;
  background-color: var(--color-background-separator-base-base);
}

.toggle-separator:first-of-type {
  display: none;
}

input[type="radio"]:checked + .toggle-item + .toggle-separator,
.toggle-item:hover + .toggle-separator {
  opacity: 0;
}

::selection {
  background: var(--color-accent-600, dodgerblue);
  color: var(--color-accent-50, white);
}
