/* base */

select {
  font-family: var(--font-family);
  font-size: var(--font-size-medium);
  line-height: var(--font-line-height-medium);

  border-radius: var(--radius-x-small);
  border-width: var(--size-none);

  padding-inline: var(--size-small);
  padding-block: var(--size-x-small);

  background: var(--color-background-input-base-idle);
  outline: 1px solid var(--color-border-input-base-idle);
  outline-offset: -1px;
  color: var(--color-text-primary);

  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

select::after {
  content: "";
  width: 40px;
  height: 40px;
  background: red;
  mask-image: url("../graphic/ArrowDownShape.svg");
  -webkit-mask-image: url("../graphic/ArrowDownShape.svg");
}

select:disabled,
select[theme-variant]:disabled {
  opacity: var(--ratio-opacity-disabled);
  cursor: not-allowed;
}

select:enabled:hover {
  background: var(--color-background-input-base-hover);
}

select:enabled:focus,
select:enabled:focus-visible {
  background: var(--color-background-input-base-hover);
  box-shadow: var(--effect-component-base-focus);
  outline: none;
}

select[theme-variant="flat"] {
  background: var(--color-background-input-flat-idle);
  outline: none;
}

select[theme-variant="flat"]:enabled:hover {
  background: var(--color-background-input-flat-hover);
}

select:enabled:focus,
select:enabled:focus-visible {
  background: var(--color-background-input-flat-hover);
}
