:root {
  --size-adjustment-factor: 1;
  --font-adjustment-factor: 1;
}

:root[theme-density="compact"] {
  --size-adjustment-factor: 0.75;
}
:root[theme-density="default"] {
  --size-adjustment-factor: 1;
}
:root[theme-density="relaxed"] {
  --size-adjustment-factor: 1.25;
}
:root[theme-size="small"] {
  --font-adjustment-factor: 0.8;
}
:root[theme-size="default"] {
  --font-adjustment-factor: 1;
}
:root[theme-size="large"] {
  --font-adjustment-factor: 1.2;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: var(--color-background-primary, #f0f0f0);
  color: var(--color-text-primary-alpha, black);
  font-size: var(--font-size-medium);

  font-family: var(--font-family), sans-serif;
  font-variant-numeric: tabular-nums slashed-zero;
  font-feature-settings: "ss01" 1;
}

.contrast-logo {
  border-radius: 2px;
  padding-inline: 4px;
  border-width: 1px;
  border-style: solid;
  user-select: none;
  margin-right: 4px;
}
