label {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: var(--size-small);
  padding-block: var(--size-x-small);
  cursor: pointer;
  user-select: none;
}

.check {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;

  width: var(--size-large);
  height: var(--size-large);
}

.check svg {
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke: var(--color-text-component-accent-idle);
  stroke-width: 0;
}

input[type="checkbox"] {
  outline: 1px solid transparent;
  appearance: none;
  padding: 0px;
  margin: 0px;
  width: var(--size-large);
  height: var(--size-large);
}

input[type="checkbox"]:checked {
  background-color: var(--color-background-component-accent-idle);
  border-width: 0px;
  box-shadow: var(--effect-convex);
}

input[type="checkbox"]:checked:hover {
  background-color: var(--color-background-component-accent-hover);
}

input[type="checkbox"]:checked:active {
  background-color: var(--color-background-component-accent-active);
}

input[type="checkbox"]:checked + .check svg {
  stroke-width: 2;
}

input[type="checkbox"]:checked:focus,
input[type="checkbox"]:checked:focus-visible {
  box-shadow: var(--effect-component-accent-focus);
  border: 1px solid var(--color-border-component-accent-focus);
}
