.app {
  display: flex;
  flex-direction: row;
  gap: var(--size-none);
  align-items: stretch;
}

.app-nav {
  display: flex;
  flex-direction: column;
  gap: var(--size-small);

  padding: var(--size-large);
}

.app-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;

  padding: var(--size-large);
}

.color-cards {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: var(--size-none);
}
