@import "helpers";

:root {
  --color-text-primary-alpha: var(--color-base-100-alpha);
  --color-text-secondary-alpha: var(--color-base-300-alpha);
  --color-text-tertiary-alpha: var(--color-base-500-alpha);

  --color-text-primary: var(--color-base-100);
  --color-text-secondary: var(--color-base-300);
  --color-text-tertiary: var(--color-base-500);

  --color-background-primary: var(--color-base-800);
  --color-background-secondary: var(--color-base-775);
  --color-background-tertiary: var(--color-base-750);

  /* state */
  --color-background-error: var(--color-error-650);
  --color-border-error: var(--color-error-600);
  --color-text-error: var(--color-error-100);

  --color-background-warning: var(--color-warning-650);
  --color-border-warning: var(--color-warning-600);
  --color-text-warning: var(--color-warning-100);

  --color-background-success: var(--color-success-650);
  --color-border-success: var(--color-success-600);
  --color-text-success: var(--color-success-100);

  /* component */
  --color-text-component-accent-idle: var(--color-accent-50);

  --color-background-component-accent-idle: var(--color-accent-500);
  --color-background-component-accent-hover: var(--color-accent-450);
  --color-background-component-accent-active: var(--color-accent-550);

  --color-border-component-base-focus: var(--color-accent-450);
  --color-border-component-accent-focus: var(--color-accent-150);

  /* button */
  --color-text-button-secondary-idle: var(--color-text-primary);

  --color-background-button-secondary-idle: var(--color-base-650);
  --color-background-button-secondary-hover: var(--color-base-625);
  --color-background-button-secondary-active: var(--color-base-675);

  --color-background-button-flat-idle: var(--color-base-700-alpha);
  --color-background-button-flat-hover: var(--color-base-650-alpha);
  --color-background-button-flat-active: var(--color-base-750-alpha);

  /* input */
  --color-background-input-base-idle: var(--color-base-750);
  --color-background-input-base-hover: var(--color-base-700);

  --color-border-input-base-idle: var(--color-base-675);

  --color-background-input-flat-idle: var(--color-base-750-alpha);
  --color-background-input-flat-hover: var(--color-base-700-alpha);

  /* table */
  --color-background-table-header: var(--color-background-secondary);
  --color-background-table-row-even: var(--color-base-800);
  --color-background-table-row-odd: var(--color-base-775);
  --color-background-table-row-even-selected: var(--color-accent-450);
  --color-background-table-row-odd-selected: var(--color-accent-450);
  --color-background-table-row-selected: var(--color-accent-450);

  @include accented("error", "100", "500", "450", "550");
  @include accented("warning", "100", "500", "450", "550");
  @include accented("success", "100", "500", "450", "550");

  /* effects */
  --effect-none: none;
  --effect-convex: inset 0px 1px 0px 0px rgba(var(--rgb-base-100), 15%);
  --effect-concave: inset 0px 1px 0px 0px rgba(0, 0, 0, 0.15);

  $offset: 1px;
  --effect-component-base-focus: 0px 0px 0px #{$offset} rgba(var(--rgb-accent-450), 35%);
  --effect-component-accent-focus: 0px 0px 0px #{$offset} rgba(var(--rgb-accent-450), 35%);
}
