label {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: var(--size-small);
  padding-block: var(--size-x-small);
  cursor: pointer;
  user-select: none;
}

.dot {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;

  width: var(--size-large);
  height: var(--size-large);
}

.dot svg circle {
  fill: none;
}

input[type="radio"] {
  outline: 1px solid transparent;
  border-radius: 99px;
  appearance: none;
  padding: 0px;
  margin: 0px;
  width: var(--size-large);
  height: var(--size-large);
}

input[type="radio"]:checked {
  background-color: var(--color-background-component-accent-idle);
  border-width: 0px;
  box-shadow: var(--effect-convex);
}

input[type="radio"]:checked:hover {
  background-color: var(--color-background-component-accent-hover);
}

input[type="radio"]:checked:active {
  background-color: var(--color-background-component-accent-active);
}

input[type="radio"]:checked + .dot svg circle {
  fill: var(--color-text-component-accent-idle);
}

input[type="radio"]:checked:focus,
input[type="radio"]:checked:focus-visible {
  box-shadow: var(--effect-component-accent-focus);
  border: 1px solid var(--color-border-component-accent-focus);
}
