/* base | secondary */

button {
  --button-background-idle: var(--color-background-button-secondary-idle);
  --button-background-hover: var(--color-background-button-secondary-hover);
  --button-background-active: var(--color-background-button-secondary-active);
  --button-color: var(--color-text-button-secondary-idle);
  --button-border-focus: var(--color-border-component-base-focus);
  --button-effect-idle: var(--effect-convex);
  --button-effect-focus: var(--effect-component-base-focus);

  //

  font-family: var(--font-family);
  font-size: var(--font-size-medium);
  line-height: var(--font-line-height-medium);
  font-weight: var(--font-weight-regular);

  padding-inline: var(--size-large);
  padding-block: var(--size-x-small);

  border-radius: var(--radius-small);

  background: var(--button-background-idle);
  box-shadow: var(--button-effect-idle);
  border: none;
  outline: 1px solid transparent;

  color: var(--button-color);
  user-select: none;
}

button:disabled {
  background: var(--button-background-idle) !important;
  opacity: var(--ratio-opacity-disabled);
  box-shadow: none;
  cursor: not-allowed;
}

@media (any-hover: hover) {
  button:hover {
    background: var(--button-background-hover);
  }
}

button:active {
  background: var(--button-background-active);
}

button:focus,
button:focus-visible {
  box-shadow: var(--button-effect-focus);
  border: 1px solid var(--button-border-focus);
  padding-inline: calc(var(--size-large) - 1px);
  padding-block: calc(var(--size-x-small) - 1px);
}

/* primary */
button[theme-variant="primary"] {
  --button-background-idle: var(--color-background-component-accent-idle);
  --button-background-hover: var(--color-background-component-accent-hover);
  --button-background-active: var(--color-background-component-accent-active);
  --button-color: var(--color-text-component-accent-idle);
  --button-effect-idle: var(--effect-convex);
  --button-effect-focus: var(--effect-component-accent-focus);
  --button-border-focus: var(--color-border-component-accent-focus);
}

/* flat */
button[theme-variant="flat"] {
  --button-background-idle: var(--color-background-button-flat-idle);
  --button-background-hover: var(--color-background-button-flat-hover);
  --button-background-active: var(--color-background-button-flat-active);
  --button-effect-idle: var(--effect-none);
}
button[theme-variant="flat"][theme-color="error"] {
  --button-background-idle: var(--color-error-500);
  --button-background-hover: var(--color-error-450);
  --button-background-active: var(--color-error-550);
  --button-color: var(--color-error-100);
}
button[theme-variant="flat"][theme-color="warning"] {
  --button-background-idle: var(--color-warning-500);
  --button-background-hover: var(--color-warning-450);
  --button-background-active: var(--color-warning-550);
  --button-color: var(--color-warning-100);
}
button[theme-variant="flat"][theme-color="success"] {
  --button-background-idle: var(--color-success-500);
  --button-background-hover: var(--color-success-450);
  --button-background-active: var(--color-success-550);
  --button-color: var(--color-success-100);
}

button[theme-size="small"] {
  font-size: var(--font-size-small);
  line-height: var(--font-line-height-small);
  font-weight: var(--font-weight-regular);
}
button[theme-size="large"] {
  font-size: var(--font-size-large);
  line-height: var(--font-line-height-large);
  font-weight: var(--font-weight-semibold);
}

