.column-container {
  display: flex;
  flex-direction: row;
  gap: var(--size-none);

  user-select: none;
  clip-path: inset(0 0 round var(--radius-small));
}

.column {
  width: 100px;
}

.cell {
  padding: var(--size-x-small) var(--size-medium);
}

.header {
  font-weight: var(--font-weight-bold);

  background: var(--color-background-table-header);
  color: var(--color-text-secondary-alpha);
}

.row[data-index="odd"] {
  background-color: var(--color-background-table-row-odd);
}

.row,
.row[data-index="even"] {
  background-color: var(--color-background-table-row-even);
}

.row[data-selected="true"] {
  background-color: var(--color-background-table-row-selected);
  color: var(--color-text-component-accent-idle);
}
