.component-row {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: var(--size-medium);
}

.component-column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--size-medium);
}

.with-padding {
  padding: 32px;
}

.rows {
  display: flex;
  flex-direction: column;
  gap: 2px;
  padding: 2px;
  background-color: yellow;

  height: 120px;
  resize: horizontal;
  overflow: auto;
}

.row {
  display: flex;
  flex-direction: row;
  gap: 8px;
  background-color: red;
  padding: 2px;

  overflow: hidden;
}

.field {
  display: flex;
  align-items: baseline;
  flex-direction: row;
  gap: 2px;
  flex-basis: 100px;
  flex-grow: 1;
  flex-shrink: 1;
  min-width: 16px;
}

.title {
  text-align: end;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;

  font-size: 16px;

  max-width: 100px;
  min-width: 16px;

  flex-basis: 16px;
  flex-shrink: 1;
  flex-grow: 1;
  background-color: green;
}

.value {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;

  font-size: 16px;

  min-width: 16px;

  flex-basis: 32px;
  flex-shrink: 1;
  flex-grow: 1;
  background-color: dodgerblue;
}
