@mixin accented($accentName, $color, $idleBackground, $hoverBackground, $activeBackground) {
  --color-background-#{$accentName}-idle: var(--color-#{$accentName}-#{$idleBackground});
  --color-background-#{$accentName}-hover: var(--color-#{$accentName}-#{$hoverBackground});
  --color-background-#{$accentName}-active: var(--color-#{$accentName}-#{$activeBackground});
}
