:root[theme="light"] {
  --color-text-primary-alpha: var(--color-base-800-alpha);
  --color-text-secondary-alpha: var(--color-base-600-alpha);
  --color-text-tertiary-alpha: var(--color-base-400-alpha);

  --color-text-primary: var(--color-base-800);
  --color-text-secondary: var(--color-base-600);
  --color-text-tertiary: var(--color-base-400);

  --color-background-primary: var(--color-base-125);
  --color-background-secondary: var(--color-base-75);
  --color-background-tertiary: var(--color-base-50);

  /* state */
  --color-background-error: var(--color-error-150);
  --color-border-error: var(--color-error-200);
  --color-text-error: var(--color-error-700);

  --color-background-warning: var(--color-warning-150);
  --color-border-warning: var(--color-warning-200);
  --color-text-warning: var(--color-warning-700);

  --color-background-success: var(--color-success-150);
  --color-border-success: var(--color-success-200);
  --color-text-success: var(--color-success-700);

  /* component */
  --color-text-component-accent-idle: var(--color-accent-50);

  --color-background-component-accent-idle: var(--color-accent-500);
  --color-background-component-accent-hover: var(--color-accent-450);
  --color-background-component-accent-active: var(--color-accent-550);

  --color-border-component-base-focus: var(--color-accent-450);
  --color-border-component-accent-focus: var(--color-accent-700);

  /* button */
  --color-text-button-secondary-idle: var(--color-text-primary);

  --color-background-button-secondary-idle: var(--color-base-50);
  --color-background-button-secondary-hover: var(--color-base-75);
  --color-background-button-secondary-active: var(--color-base-150);

  --color-background-button-flat-idle: var(--color-base-50-alpha);
  --color-background-button-flat-hover: var(--color-base-75-alpha);
  --color-background-button-flat-active: var(--color-base-150-alpha);

  /* input */
  --color-background-input-base-idle: var(--color-base-75);
  --color-background-input-base-hover: var(--color-base-50);

  --color-border-input-base-idle: var(--color-base-200);

  --color-background-input-flat-idle: var(--color-base-75-alpha);
  --color-background-input-flat-hover: var(--color-base-50-alpha);

  /* table */
  --color-background-table-header: var(--color-background-secondary);
  --color-background-table-row-even: var(--color-base-125);
  --color-background-table-row-odd: var(--color-base-150);
  --color-background-table-row-selected: var(--color-accent-450);

  /* effects */
  --effect-convex: inset 0px -1px 0px 0px rgba(0, 0, 0, 0.2);
  --effect-concave: inset 0px 1px 0px 0px rgba(0, 0, 0, 0.1);

  --effect-component-base-focus: inset 0px 0px 0px 1px var(--color-border-component-base-focus);
  --effect-component-accent-focus: inset 0px 0px 0px 1px var(--color-border-component-base-focus),
    inset 0px 0px 0px 2px var(--color-text-component-accent-idle);
}
