:root {
  --font-family: "Roboto";

  --font-size-small: calc(10px * var(--font-adjustment-factor, 1));
  --font-size-medium: calc(12px * var(--font-adjustment-factor, 1));
  --font-size-large: calc(14px * var(--font-adjustment-factor, 1));

  --font-line-height-small: calc(12px * var(--font-adjustment-factor, 1));
  --font-line-height-medium: calc(16px * var(--font-adjustment-factor, 1));
  --font-line-height-large: calc(20px * var(--font-adjustment-factor, 1));

  --font-weight-regular: 400;
  --font-weight-semibold: 500;
  --font-weight-bold: 700;
}
