:root {
  --size-100: calc(0px * var(--size-adjustment-factor, 1));
  --size-200: calc(1px * var(--size-adjustment-factor, 1));
  --size-300: calc(2px * var(--size-adjustment-factor, 1));
  --size-400: calc(4px * var(--size-adjustment-factor, 1));
  --size-500: calc(8px * var(--size-adjustment-factor, 1));
  --size-600: calc(16px * var(--size-adjustment-factor, 1));
  --size-700: calc(32px * var(--size-adjustment-factor, 1));
  --size-800: calc(64px * var(--size-adjustment-factor, 1));
  /* extra */
  --size-550: calc(12px * var(--size-adjustment-factor, 1));
  --size-650: calc(24px * var(--size-adjustment-factor, 1));

  --size-none: var(--size-100);
  --size-xx-small: var(--size-200);
  --size-x-small: var(--size-300);
  --size-small: var(--size-400);
  --size-medium: var(--size-500);
  --size-large: var(--size-600);
  --size-x-large: var(--size-700);
  --size-xx-large: var(--size-800);
}
